.thank-you {
  width: 441px;
  position: relative;
  line-height: 107px;
  display: inline-block;
  flex-shrink: 0;
  max-width: 100%;
  z-index: 1;
}
.thank-you-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl) 0 var(--padding-5xl);
  box-sizing: border-box;
  max-width: 100%;
  color: var(--color-black);
  font-family: var(--font-roboto-condensed);
}
.if-you-liked {
  position: relative;
  font-weight: 500;
  z-index: 1;
}
.if-you-liked-this-project-the-wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-53xl);
  box-sizing: border-box;
  max-width: 100%;
  font-size: 30.8px;
}
.dont-foget-to {
  position: relative;
  font-size: var(--font-size-9xl-1);
  font-weight: 500;
  z-index: 1;
}
.frame-parent52,
.project {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.frame-parent52 {
  width: 761px;
  flex-direction: column;
  justify-content: flex-start;
  gap: 29.1px;
}
.project {
  width: 1119.7px;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  font-size: 106.8px;
  color: var(--color-darkgray-200);
  font-family: var(--font-poppins);
}
.get-more-premium {
  white-space: pre-wrap;
}
.free {
  font-size: 61px;
}
.get-more-premium-container {
  width: 821px;
  position: relative;
  font-weight: 500;
  display: inline-block;
  max-width: 100%;
  z-index: 1;
}
.getem-from-our {
  align-self: stretch;
  position: relative;
  text-decoration: underline;
  font-weight: 500;
  color: inherit;
  z-index: 1;
}
.community,
.designs {
  display: flex;
  align-items: flex-start;
}
.community {
  width: 239px;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-xs) 0 0;
  box-sizing: border-box;
}
.designs {
  align-self: stretch;
  flex-direction: row;
  justify-content: space-between;
  max-width: 100%;
  gap: var(--gap-xl);
  text-align: center;
  font-size: var(--font-size-9xl-1);
  color: var(--color-white);
  font-family: var(--font-poppins);
}
