.image-937-icon,
.image-938-icon {
  position: absolute;
  top: 448px;
  left: 1276.1px;
  width: 60.3px;
  height: 60.3px;
  object-fit: contain;
  z-index: 2;
}
.image-938-icon {
  left: 978px;
  width: 61.1px;
  height: 61.1px;
  z-index: 1;
}
.gallery {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.thankyou {
  width: 100%;
  position: relative;
  background-color: #f7f7f7;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 88px var(--padding-141xl) 68px var(--padding-61xl);
  box-sizing: border-box;
  gap: 107.8px;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1325px) {
  .designs {
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media screen and (max-width: 800px) {
  .thank-you {
    font-size: var(--font-size-34xl);
    line-height: 64px;
  }
  .if-you-liked {
    font-size: var(--font-size-6xl);
  }
  .if-you-liked-this-project-the-wrapper {
    padding-left: var(--padding-17xl);
    padding-right: var(--padding-17xl);
    box-sizing: border-box;
  }
  .get-more-premium-container {
    font-size: 49px;
  }
  .thankyou {
    gap: var(--gap-35xl);
    padding-left: var(--padding-21xl);
    padding-right: var(--padding-61xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .thank-you {
    font-size: var(--font-size-13xl);
    line-height: 43px;
  }
  .if-you-liked {
    font-size: var(--font-size-lg);
  }
  .dont-foget-to {
    font-size: var(--font-size-3xl);
  }
  .get-more-premium-container {
    font-size: var(--font-size-18xl);
  }
  .getem-from-our {
    font-size: var(--font-size-3xl);
  }
  .thankyou {
    gap: var(--gap-8xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
